import hTimePicker from "@/components/h-timePicker/h-timePicker.vue";
 
export default {
	components: { hTimePicker },
	data() {
		return {
			claimlist:[],																						//课程
			selectStudentList:[1,2,3],															//选中学生列表
			selectTime:'',																					//选中的时间
			workClaim:'',																						//作业要求
			isSelectCourse:false,																		//是否选中看视频
			selectTypes:[],																					//选中的作业
			workType:[{
				type:0,
				value:'看视频'
			},{
				type:1,
				value:'文字回答'
			},{
				type:2,
				value:'上传视频'
			},{
				type:3,
				value:'上传图片'
			}
			]
		};
	},
	methods:{
		//去选择课程
		selectCourse(){
			uni.navigateTo({
				url:'/pages/coursesTaken/classIsOver/selectCourse'
			})
		},
		//选择时间
		changeTime(e){
			this.selectTime = e
		},
		// 选择作业类型
		selectType(e){
			console.log(e)
			if(e.detail.value==0){
				this.isSelectCourse = true
			}
			this.selectTypes.forEach(item=>{
				if(!this.selectTypes.includes(item)){
					this.selectTypes.push(item)
				}
			})
		}
	}
}
